* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

.home,
.reports,
.products {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}


table{
  border-radius: 20px;
}

tr td{
  cursor: pointer;
}

input[type='submit'], button.btn, input[type='reset'] {
  border-radius: 20px;
}

section.table div.col{
  border-right: 1px solid black;
  height: 100px;
}
div.period {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 70px;
  padding-left: 0px;
  justify-content: center;
  align-content: center;
  margin-left: 0px;
}

/*div.period > span#day{
  justify-self: center;
}*/

div.period span.mid {
  align-self: center;
  font-weight: bold;
  font-size: 1.2em;
}

div.period span.start {
  font-size: 0.7em;
  align-self: flex-start;
}

section.row.table-row {
  border: 1px solid black;
}


.nav.nav-tabs a.nav-link {
  text-decoration: none;
  color: #666666;
}

.nav.nav-tabs a.nav-link.active {
  font-weight: bold;
  color: #001;
}

.jumbotron {
  background-color: rgba(243, 233, 243, 0.61);
}


thead.thead-dark {
  color: white;
}

.period-list {
  list-style: none;
  padding: 5px 10px;
  background-color: #000000;
  border-radius: 10px;
  color: #f8f7ff;
  font-weight: bold;
}


@media (min-width: 992px) {
  .period-list {
    /*padding: 5px 13px;*/
    width: 30%;
    text-align: center;
  }
}


legend {
  margin-top: 20px;
  margin-bottom: 20px;
  text-align: center;
}

label {
  margin-bottom: 8px;
  margin-top: 10px;
  font-weight: bold;
  font-size: 12px;
}


aside.nav-aside > ul {
  display: flex;
  flex-direction: column;
  height: 30%;
  position: fixed;
  right: 10px;
  flex-grow: 1;
}

aside.nav-aside > ul > li {
  margin-top: 15px;
  background-color: black /*#000011*/;
  color: #f8f7ff;
  border: 4px solid orange;
  padding: 7px 15px;
  border-radius: 20px;
  box-shadow: 0 10px 6px -6px orange;
}

.link {
  color: #f8f7ff;
  font-weight: bold;
}

svg path {
  fill: #f8f7ff;
}

.bg-theme{
  background-color: #fff;
  color:#f5a700;
  font-weight: bold;
  border:1px solid #f5a700;
}
.bg-theme-btn{
  background-color:black;
  color:#f5a700;
  border:3px solid #f5a700;
  font-weight: bold;
  width:200px;
}

.bg-success-btn{
  background-color:white;
  color: #06b100;
  border:3px solid #06b100;
  font-weight: bold;
  width:200px;
}
.bg-success-btn svg path{
  fill:#06b100;
}
.bg-success-btn:hover svg path{
  fill:#fff;
}
.bg-warning-btn{
  background-color:white;
  color: #f5a700;
  border:3px solid #f5a700;
  font-weight: bold;
  width:200px;
}
.bg-warning-btn svg path{
  fill: #f5a700;
}
.bg-warning-btn:hover svg path{
  fill:#fff;
}
